<template>
  <div class="market-wrapper header-page">
    <Header :title="$t('market.market')" :leftArrow="false">
      <template #right>
        <VanIcon name="search" size="18" color="#3E536C" />
      </template>
    </Header>
    <div class="nav">
      <!-- 板块选择 -->
      <div class="tab-box">
        <div
          class="item"
          v-for="(item, index) in typeList"
          @click="HandleHeaderClik"
          :key="index"
          :class="{ active: index === 1 }"
        >
          <span>{{ item.value }}</span>
        </div>
      </div>
      <!-- 货币选择 -->
      <div class="currency-box">
        <div
          class="item"
          v-for="(item, index) in currencyList"
          @click="HandleHeaderClik"
          :key="index"
          :class="{ active: index === 0 }"
        >
          {{ item }}
        </div>
      </div>
      <!-- 排序选择 -->
      <div class="nav-box">
        <div class="nav-box-left">
          <div class="nav-box-left-name">
            {{ $t("market.name") }}
            <van-icon class="nav-icon" :name="sort" size="10"></van-icon>
          </div>
          <div class="nav-box-left-time">
            {{ $t("market.24time") }}
            <van-icon class="nav-icon" :name="sort" size="10"></van-icon>
          </div>
        </div>
        <div class="nav-box-center">
          {{ $t("market.newPrice")
          }}<van-icon class="nav-icon" :name="sort" size="10"></van-icon>
        </div>
        <div class="nav-box-right">
          {{ $t("market.upDownRange")
          }}<van-icon class="nav-icon" :name="sort" size="10"></van-icon>
        </div>
      </div>
    </div>

    <div class="content">
      <div
        class="row"
        v-for="(item, index) in marketDataKey"
        :key="index"
        :class="getPercentClass(item)"
      >
        <!-- @click="toLine(item)" -->
        <div class="left">
          <p class="currency">
            {{ item.base_currency }}
            <span class="currency-tips"> /{{ item.quote_currency }} </span>
          </p>
          <p class="tips">
            {{ $t("market.24time") }}
            {{
              $n(
                BigNumber(item.quote_price)
                  .times(item.trans_amount)
                  .toNumber(),
                "currency"
              )
            }}
          </p>
        </div>
        <div class="text-center center">
          <p class="price">{{ item.quote_price }}</p>
          <p class="cny">
            {{ $n(BigNumber(item.cny_price).toNumber(), "currency") }}
          </p>
        </div>
        <div class="text-right right">
          <div class="text-center box">
            {{ getPercentText(item) }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Header";
import { getTickers } from "@/http/modules/home";
import { Toast, Icon as VanIcon } from "vant";
import { mapActions, mapState } from "vuex";
import BigNumber from "bignumber.js";

import sort from "@/assets/icons/market_selected_default.png";
import sortUp from "@/assets/icons/market_selected_up_light.png";
import sortDown from "@/assets/icons/market_selected_down_light.png";

export default {
  name: "market",
  components: {
    Header,
    VanIcon
  },
  data() {
    return {
      sort,
      sortUp,
      sortDown,
      tickersList: [],
      timerout: "",
      // ws键值对
      msgsKeys: [],
      // 行情数据 object，方便ws获取的数据实时替换
      // marketData: {},
      BigNumber
    };
  },
  computed: {
    ...mapState({
      ticker_price: state => state.home.ticker_price,
      marketData: state => state.home.marketData
    }),
    marketDataKey() {
      // return Object.keys(this.marketData);
      return this.marketData;
    },
    typeList() {
      return [
        {
          name: 1,
          value: this.$t("market.type1")
        },
        {
          name: 2,
          value: this.$t("market.type2")
        },
        {
          name: 3,
          value: this.$t("market.type3")
        },
        {
          name: 4,
          value: this.$t("market.type4")
        },
        {
          name: 5,
          value: this.$t("market.type5")
        }
      ];
    },
    currencyList() {
      return ["DCEP", "HUSD", "BTC", "ETH"];
    }
  },
  activated() {
    this.init();
    // this.timer();
  },
  deactivated() {
    // this.socketClose();
    // clearInterval(this.timerout);
  },
  methods: {
    ...mapActions({
      GetTotalEarnings: "mine/GetTotalEarnings",
      sendSocketMsg: "home/sendSocketMsg",
      setReceiveFun: "home/setReceiveFun"
    }),
    async init() {
      let res = await getTickers();
      if (res && res.status === 1) {
        this.tickersList = res.data;
        this.msgsKeys = Object.keys(res.data).map(item => {
          return res.data[item].symbol;
        });
        // this.getSocket();
      }
      // this.timer();
    },
    // 获取websocket数据 行情
    getSocket() {
      const recieveFunc = msgData => {
        if (msgData.data && msgData.data.symbol) {
          console.log(msgData.data.symbol);
          this.$set(this.marketData, msgData.data.symbol, msgData.data);
          console.log(this.marketData);
          // obj = [msgData.data.symbol] = msgData.data;
          // this.$set(this, "marketData", obj);
        }
      };
      this.setReceiveFun(recieveFunc);
      const obj = {
        msgs: this.getMsgs(),
        timeout: 10 * 1000
      };
      this.sendSocketMsg(obj);
    },
    // 获取ws消息
    getMsgs(type) {
      return this.msgsKeys.map(item => {
        return {
          key: `ticker_${item}${type === "cancel" ? "_cancel" : ""}`,
          ts: new Date().getTime()
        };
      });
    },
    // 关闭socket
    socketClose() {
      // 关闭消息。
      // 需要发送的消息
      const msgs = this.getMsgs("cancel");
      this.sendSocketMsg(msgs);
    },
    // 人民币汇率
    // getDECPtoCNY() {
    //   getDECPtoCNY().then((res) => {
    //     this.DECPtoCNY = res.data.DECPCNY;
    //   });
    // },
    HandleHeaderClik() {
      Toast(this.$t("common.pleaseExpected"));
    },
    // eslint-disable-next-line no-unused-vars
    toLine(item) {
      this.$router.push({
        path: "kLine",
        query: item
      });
    },
    timer() {
      clearInterval(this.timerout);
      this.timerout = setInterval(() => {
        getTickers().then(res => {
          if (res && res.status === 1) {
            this.tickersList = res.data;
          }
        });
      }, 5 * 60 * 1000);
    },
    getPercent(ticker) {
      return ((ticker.quote_price - ticker.open) / ticker.open) * 100 || 0;
    },
    getPercentText(ticker) {
      // var percent = this.getPercent(ticker);
      var percent = ticker.chg;
      if (percent == 0) {
        return "0.00%";
      } else {
        return (percent > 0 ? "+" : "") + percent + "%";
      }
    },
    getPercentClass(ticker) {
      // var percent = this.getPercent(ticker);
      var percent = ticker.chg;
      if (percent < 0) {
        return "down";
      } else if (percent > 0) {
        return "up";
      }
    }
  }
};
</script>
